import vueDragConfigLayout from './drag-config-layout.vue'
const dragConfigLayout = {
  install(Vue, options) {
    Vue.component(vueDragConfigLayout.name, vueDragConfigLayout) // vueDragConfigLayout.name name
    //  this.$xxx  
    // Vue.prototype.$xxx  //  this.$xxx 
    // $  
  }
}
if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.use(dragConfigLayout);
}
export default dragConfigLayout;



